.sidebar {
    position: fixed;
    color: black;
    display: none;
    min-width: 317px;
    min-height: 100%;
    background-color: #fff;
    z-index: 9999;
}

.sidebar.open {
    display: block;
}

.background {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    padding-top: 65px;
    padding-left: 70px;
}

.nav a {
    color: black;
    font-weight: 600;
    font-size: 22px;
    text-decoration: none;
}

.nav a:hover {
    color: rgb(88, 172, 255);
}

.nav a.active {
    color: rgb(0, 89, 178);
}
.close {
    position: absolute;
    margin: 16px;
    right: 0;
    color: black;
    background: #fff;
    border: none;
}

.close img {
    width: 24px;
    height: 24px;
}
