.button {
    color: rgb(255, 255, 255);
    font-weight: 600;
    font-size: 14px;
    width: 132px;
    height: 30px;
}

.disabled {
    background-color: #004d99;
    opacity: 0.5;
    color: black;
    pointer-events: none;
}

.secondary {
    color: rgb(0, 102, 204);
    border: 1px solid #0066cc;
}

.default {
    background-color: rgb(0, 102, 204);
}

.red {
    background-color: rgb(204, 20, 57);
}

.default:hover {
    background-color: #0080ff;
}

.red:hover {
    background-color: #ff1a47;
}

.secondary:hover {
    background-color: #e5f0fa;
}

.default:active {
    background-color: #0059b3;
}

.secondary:active {
    background-color: #c9e5ff;
}

.red:active {
    background-color: #b31232;
}

.button:disabled {
}
